<template>
  <div class="index row">  
      <!-- 左侧 -->
      <div class=" leftsec">
        <div class="left_top column">
            <div class="left_top_t row ">
                <div class=" column ">
                    <p class="color_default font_16">客控中控点数</p>
                    <p class="color_fff font_30">4528</p>
                </div> 
                <div class="column">
                    <p class="color_default font_16">客控触发次数</p>
                    <p class="color_fff font_30">8528</p>
                </div>
            </div>
            <div class="left_top_t row">
                <div class=" column ">
                    <p class="color_default font_16">智能门锁在线</p>
                    <p class="color_fff font_30">2568</p>
                </div>
                <div class="column">
                    <p class="color_default font_16">门锁触发次数</p>
                    <p class="color_fff font_30">10528</p>
                </div>
            </div>
        </div>
        <div class="left_center">
            <p class="color_fff font_14 text_center">使用PMS酒店</p>
        </div>
        <div class="left_bot">
            <div id="zzjsaleCharts" :style="{width: '100%', height: '100%' }"></div>
        </div>
      </div>
      <!-- 中部 -->
      <div class="sec_center">
        <div class="center_top row text_center">
                <div class=" column flex-1">
                    <p class="color_default font_16">PMS在用酒店总数</p>
                    <p class="color_fff font_30">4528</p>
                </div> 
                <div class="column flex-1">
                    <p class="color_default font_16">PMS总模块数</p>
                    <p class="color_fff font_30">128</p>
                </div>
                <div class="column flex-1">
                    <p class="color_default font_16">PMS总销售额</p>
                    <p class="color_fff font_30">1528</p>
                </div>
        </div>
        <div class="center_c">
            <div id="center_c_echarts" :style="{ width: '100%', height: '100%' }"></div>
        </div>
        <div class="center_bot row">
            <div class="center_bot_left">
                <div id="center_bot_left_echarts" :style="{ width: '100%', height: '100%' }"></div>
            </div>
            <div class="center_bot_right">
                <div id="center_bot_right_echarts" :style="{ width: '100%', height: '100%' }"></div>
            </div>
        </div>
      </div>

    <!-- 右侧 -->
      <div class=" leftsec">
        <div class="left_top">
            <div id="right_top_echarts" :style="{ width: '100%', height: '100%' }"></div>
        </div>
        <div class="left_center">
            <p class="color_fff font_20 text_center">OTA订单</p>
        </div>
        <div class="left_bot">

        </div>
      </div>
  </div>
</template>

<script>
var echarts = require('echarts');
export default {
  name: 'index',
  data(){
    return{

    }

  },
  methods:{
      /* 自助机销售情况 */
      drawzzjSale(){
        let myChart = echarts.init(document.getElementById('zzjsaleCharts'));
        myChart.setOption({
            color: ['#3398DB'],
            tooltip: {
                trigger: 'axis',
                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                    type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    data: ['立式自助机', '壁挂式自助机'],
                    axisTick: {
                        alignWithLabel: false
                    },
                    axisLine:{
                        lineStyle:{
                            color:'#FFFFFF',
                        }
                    }
                }
            ],
            yAxis: [
                {
                    name:'年销售台数',
                    type: 'value',
                    axisLine:{
                        lineStyle:{
                            color:'#FFFFFF',
                        }
                    }
                }
            ],
            series: [
                {
                    name: '销售量',
                    type: 'bar',
                    barWidth: '60%',
                    data: [390, 220]
                }
            ]
        });
      },
      /* 整体销售情况折线图 */
      drawallSale(){
        let myChart = echarts.init(document.getElementById('center_c_echarts'));
        myChart.setOption({
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data: ['PMS', '自助机', '客控', '梯控', '门锁']
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            toolbox: {
                feature: {
                    saveAsImage: {}
                }
            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
                    axisLine:{
                        lineStyle:{
                            color:'#FFFFFF',
                        }
                    }
            },
            yAxis: {
                type: 'value',
                    axisLine:{
                        lineStyle:{
                            color:'#FFFFFF',
                        }
                    }
            },
            series: [
                {
                    name: 'PMS',
                    type: 'line',
                    stack: '总量',
                    data: [50, 122, 151, 184, 190, 210, 210, 232, 301, 434, 490, 630]
                },
                {
                    name: '自助机',
                    type: 'line',
                    stack: '总量',
                    data: [60, 182, 291, 334, 490, 530, 710, 832, 901, 1034, 1290, 1430]
                },
                {
                    name: '客控',
                    type: 'line',
                    stack: '总量',
                    data: [108, 332, 501, 654, 890, 930, 1210, 1392, 1601, 1834, 2090, 2230]
                },
                {
                    name: '梯控',
                    type: 'line',
                    stack: '总量',
                    data: [220, 382, 451, 560, 690, 830, 910, 1032, 1201, 1344, 1590, 1830]
                },
                {
                    name: '门锁',
                    type: 'line',
                    stack: '总量',
                    data: [150, 232, 451, 564, 690, 1030, 910, 932, 1101, 1334, 1620, 1830]
                },
              
            ]
        });
      },
      /* 整体产品销售情况柱状图 */
      drawallzztSale(){
        let myChart = echarts.init(document.getElementById('right_top_echarts'));
        myChart.setOption({
            color: ['#3398DB'],
            tooltip: {
                trigger: 'axis',
                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                    type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    data: ['自助机', '客控', '门锁', '梯控', 'PMS'],
                    axisTick: {
                        alignWithLabel: false
                    },
                    axisLine:{
                        lineStyle:{
                            color:'#FFFFFF',
                        }
                    }
                }
            ],
            yAxis: [
                {
                    name:'年销售数量',
                    type: 'value',
                    axisLine:{
                        lineStyle:{
                            color:'#FFFFFF',
                        }
                    }
                }
            ],
            series: [
                {
                    name: '销售量',
                    type: 'bar',
                    barWidth: '60%',
                    data: [390, 420, 590, 680, 820]
                }
            ]
        });
      },
      /* 整体产品酒店使用情况柱状图 */
      drawalluse(){
        let myChart = echarts.init(document.getElementById('center_bot_left_echarts'));
        myChart.setOption({
            color: ['#3398DB'],
            tooltip: {
                trigger: 'axis',
                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                    type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    data: ['自助机', '客控', '门锁', '梯控', 'PMS'],
                    axisTick: {
                        alignWithLabel: false
                    },
                    axisLine:{
                        lineStyle:{
                            color:'#FFFFFF',
                        }
                    }
                }
            ],
            yAxis: [
                {
                    name:'使用酒店数',
                    type: 'value',
                    axisLine:{
                        lineStyle:{
                            color:'#FFFFFF',
                        }
                    }
                }
            ],
            series: [
                {
                    name: '销售量',
                    type: 'bar',
                    barWidth: '60%',
                    data: [390, 420, 590, 680, 820]
                }
            ]
        });
      },
      /* 整体产品酒店使用情况扇形图 */
      drawalluseshan(){
        let myChart = echarts.init(document.getElementById('center_bot_right_echarts'));
        myChart.setOption({
            title: {
                text: '产品售卖占比率',
                left: 'center',
                textStyle: { 
                    color: "#fff",  
                },
            },
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b} : {c} ({d}%)'
            },
        
            series: [
                {
                    name: '访问来源',
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '60%'],
                    data: [
                        {value: 1335, name: '自助机'},
                        {value: 310, name: 'PMS'},
                        {value: 634, name: '客控'},
                        {value: 135, name: '梯控'},
                        {value: 548, name: '门锁'}
                    ],
                    emphasis: {
                        itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        })
;
      },
        
  },

  created(){
    window.onresize = function () {
        echarts.init(document.getElementById('zzjsaleCharts')).resize();// 自助机销售情况统计柱状图
        echarts.init(document.getElementById('center_c_echarts')).resize(); // 整体产品销售折线图
        echarts.init(document.getElementById('right_top_echarts')).resize(); // 整体产品销售柱状图
        echarts.init(document.getElementById('center_bot_left_echarts')).resize(); // 整体产品酒店使用情况柱状图
        echarts.init(document.getElementById('center_bot_right_echarts')).resize(); //整体产品酒店使用情况扇形图
    }
  },
  mounted(){
      this.drawzzjSale();
      this.drawallSale();
      this.drawallzztSale();
      this.drawalluse();
      this.drawalluseshan();
  }

}
</script>

<style scoped>

.index{
    width: 100%;
    height: 100%;
    min-height: 10.8rem;
    padding: 1.34rem .73rem .8rem .8rem;
    background: url('../img/bg.png') no-repeat 100% 100%;
    background-size: 100% 100%;
    overflow: hidden;
}
.leftsec{
    width:22%;
}
.left_top{ 
    height:2.74rem;
    padding: .07rem;
    border:2px solid rgba(8,200,242,1);
}
.left_top_t{
    width:96%;
    margin: auto;
    height:45%;
    background:rgba(10,61,116,1);
    border:1px solid rgba(8,200,242,0.5);
}
.left_top_t .column{
    width:50%;
    margin: .28rem 0;
    text-align: center;
}
.left_center{ 
    height:3.2rem;
    margin: .2rem 0;
    border:2px solid rgba(8,200,242,1);
}
.left_center p{ 
    line-height: .4rem;
}
.left_bot{ 
    height:2.7rem;
    margin: .2rem 0;
    border:2px solid rgba(8,200,242,1);
}

.sec_center{
    width: 46%;
    margin: 0 5%;
}
.center_top{ 
    height:1.3rem; 
    padding: .2rem ;
    border:2px solid rgba(8,200,242,1);
}
.center_top p{ 
    line-height: .4rem;
}
.center_c{
    width: 100%;
    height:4.7rem; 
    margin: .3rem 0;
    border:2px solid rgba(8,200,242,1);
}
.center_bot_left, .center_bot_right{
    width: 48%;
    height:2.4rem; 
    border:2px solid rgba(8,200,242,1);
}
.center_bot_left{
    margin-right: 4%;
}
</style>


